.lines{
    border-bottom: 1px solid #ddd;
}

.todo_Create .one{
    height: 50px;
    line-height: 50px;
    background-color: #FAFBFD;
    padding: 0 10px;
}
.todo_Create .one img{
    width: 10px;
    height: 20px;
    top: 6px;
    position: relative;
}
.todo_Create .one .weui-flex__item{
    text-align: center;
}
.todo_Create .one .placeholder{
    font-weight: 600;
}
.todo_Create .weui-panel:before{
    content: none;
}
.todo_Create .weui-panel{
    margin-top: 0;
}
.todo_Create .weui-panel__hd{
    height: 10px;
    background-color: #F1F2F6;
}
.todo_Create .line{
    width: 90%;
    margin-left: 5%;
    border-bottom: 1px solid #C6C7C9;
}
.todo_Create .two .weui-cell:nth-child(1){
     padding: 10px 20px;
}
.todo_Create .two .weui-cell input{
    color: #C6C7C9;
    font-size: 14px;
}
.todo_Create .two .weui-cell:nth-child(1) input{
    margin: 0 -14px;
}
.todo_Create .two .weui-cell:nth-child(2) {
    padding: 10px 25px;
}
.todo_Create .three .weui-cells:before {
    content: none;
}
.todo_Create .three .weui-cells{
    margin-top: 0;
}
.todo_Create .three .three_left1,
.todo_Create .three .three_left2{
    position: relative;
    left: -17px;
}
.todo_Create .three .weui-cell_access .datetime:after{
    content: none;
}
.todo_Create .three .weui-cell_access .datetime input,
.todo_Create .three .weui-cell_access .three_right input{
    text-align: right;
    position: relative;
    right: -22px;
}
.todo_Create .three .weui-cell_access .three_right .weui-select {
    line-height: 80px;
    width: 80px;
}
.todo_Create .three .weui-cell_access .weui-cell__ft:after {
    top: 40px;
    right: -16px;
}
.todo_Create .layui-input-inline input{
    text-align: right;
}

.todo{
    overflow: hidden;
}
.todo .one{
    line-height: 30px;
    padding: 3px 16px;
    background-color: #F4F4F4;
}
.todo .one .weui-flex__item ,
.detail .one .weui-flex__item{
    text-align: center;
    color: #333;
    font-weight: 600;
}
.todo .one div:nth-child(3) .placeholder{
    color: #333;
    font-weight: 600;
}

.todo .two{
    font-size: 12px;
    padding: 0px 10px;
    line-height: 30px;
    color: #929292;
    background-color: #F2F1F6;
}
.todo .two Link,
.todo .two Link:hover{
    color: #929292;
}

.three .weui-cells:before{
    content: none;
}
.three .weui-cells{
    padding: 0px 10px;
}
.three .three_left{
    margin-left: -18px;
}
.three .three-left{
    font-size: 16px;
    margin-left: 10px;
}
.three .three_right{
    font-size: 14px;
    color: #929292;
    position: absolute;
    right: 18px;
    top: -10px;
    width: 40px;
}

.index .weui-cell{
    width: 100%;
}
.index .weui-cells{
    margin-top: 0;
}
.index .index_top{
    /*width: 20px;*/
    /*height: 20px;*/
    line-height: 50px;
}
.index .index_top input{
    width: 9px;
    height: 15px;
    padding: 0 5px;
    border: 1px solid #969896;
}

.index .index_middle {
    position: relative;
    left: 6px;
}
.index .index_middle .weui-cell__bd{
    color: #333;
    width: 222px;
}
.index .index_middle .weui-cell__ft{
    font-size: 12px;
    padding: 0;
}
.index .weui-cell:before{
    content: none;
}

.index .index_right{
    position: relative;
    left: -2px;
    font-size: 12px;
    padding: 0px 7px;
    margin: -1px 0px;
    width: 154px;
    text-align: right;
}
.index .index_right .link{
    color: #969896;
    margin: auto 2px;
}
.index .index_right .link:active{
    color: #969896;
}

.index .weui-cell,
.index .weui-select,
.index .weui-cells .weui-cell__bd{
    color: #969896;
    font-size: 14px;
    padding: 0px 7px;
    margin: -1px 0px;
}
.index .weui-select{
    left: -22px;
}
.index .weui-cell_access .weui-cell__ft:after{
    content: none;
}
.index .weui-cells:after{
    content: none;
}
.index option{
    border: none;
}
.weui-icon-checked{
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1px solid #eee;
}

.detail .one,
.update .one{
    line-height: 30px;
    padding: 3px 16px;
    background-color: #F4F4F4;
    font-weight: 600;
    text-align: center;
}
.detail .two,
.update .two{
    height: 30px;
    background-color: #F2F1F6;
}
.detail .two .success,
.update .two .success{
    color: #929292;
    line-height: 30px;
    font-size: 14px;
    padding: 0 15px;
}
.detail .three,
.update .three {
    line-height: 38px;
}
.detail .three .three_lc,
.update .three .three_lc{
    text-align: right;
    padding: 0 20px
}

