.Data{
    position: fixed;
    background-color: #F3F1F7;
    height: 100%;
    width: 100%;
}
.Data .one_right{
    font-size: 14px;
    text-align: right;
    padding: 15px 24px;
    color: #4F8DE5;
    font-weight: 600;
}
.Data .flag{
    width: 12px;
    height: 15px;
    position: relative;
    top: 18px;
    left: 23px;
    color: #929292;
}
.Data .info{
    position: relative;
    top: 15px;
    left: 25px;
    font-size: 14px;
}
.Data .weui-search-bar{
    background-color: #F3F1F7;
}
.Data .weui-search-bar__form {
    margin: 0 16px;
}
.Data .weui-search-bar__label{
    background-color: #E5E3EA;
    color: #929292;
}
.Data .weui-search-bar__label .weui-icon-search {
    position: absolute;
    left: 5px;
}
.Data .weui-search-bar__label span {
    position: absolute;
    left: 21px;
}
.Data .three{
    height: 80px;
    padding: 10px 20px;
}
.Data .three .weui-flex__item{
    background: #fff;
    border-radius: 16px;
    position: relative;
}
.Data .three .weui-flex__item:nth-child(1) img{
    position: absolute;
    top: 14px;
    left: 15px;
}
.Data .three .weui-flex__item:nth-child(1) span{
    position: absolute;
    top: 10px;
    right: 15px;
    color: #333;
    font-weight: 700;
    font-size: 22px;
}
.Data .three .weui-flex__item:nth-child(1) .placeholder:nth-child(2){
    position: absolute;
    top: 50px;
    left: 18px;
    font-size: 14px;
    color: #909091;
}
.Data .three .weui-flex__item:nth-child(2){
    margin-left: 15px;
}
.Data .three .weui-flex__item:nth-child(2) img{
    position: absolute;
    top: 14px;
    left: 15px;
}
.Data .three .weui-flex__item:nth-child(2) span{
    position: absolute;
    top: 10px;
    right: 15px;
    color: #333;
    font-weight: 700;
    font-size: 22px;
}
.Data .three .weui-flex__item:nth-child(2) .placeholder:nth-child(2){
    position: absolute;
    top: 50px;
    left: 18px;
    font-size: 14px;
    color: #909091;
}
