.userinfo{
    padding-top: 5px;
    font-size: 16px;
}
.userinfo .header{
    display: flex;
    width: 100%;
    margin: 5px 0;
}
.userinfo .header span{
    flex: 1;
}
.userinfo .header h2{
    text-align: center;
}
.userinfo .nav{
    height: 24px;
    color: #929292;
    background-color: #F1F1F1;
}
.userinfo .header .flag{
    width: 16px;
    height: 24px;
    position: relative;
    top: 9px;
    left: 4px;
}
.userinfo .flagb{
    width: 20px;
    height: 30px;
}
.userinfo .weui-flex{
    display: flex;
    padding: 9px;
    font-size: 14px;
}
.userinfo .weui-flex .weui-flex__item{
    flex: 1 1;
}
.userinfo .weui-flex .weui-flex__item:nth-child(1){
    line-height: 50px;
    font-size: 16px;
}
.userinfo .weui-flex .weui-flex__item:nth-child(2){
    text-align: right;
    position: relative;
    top: 10px;
}
.userinfo .weui-flex .weui-flex__item span{
    color: #929292;
    position: absolute;
    top: 2px;
    font-size: 16px;
    right: 20px;
}
.userinfo  .lines{
    border-bottom: 1px solid #f6f5f5;
}