.sy{
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: lightgoldenrodyellow;
}
.sy .one{
    font-size: 30px;
    text-align: center;
    margin: 80px 0;
}
.sy .one .weui-badge{
    height: 80px;
    width: 80px;
    line-height: 80px;
    border-radius: 100%;
    font-size: 20px;
    background-color: #1196ff;
    position: relative;
    top: 20%;
}

.sy .weui-btn-area{
    display: flex;
}
.sy .weui-btn-area .link{
    flex: 1 1;
    font-size: 20px;
    font-weight: 500;
    color: #fff;
}

.sy .weui-btn-area .link .weui-btn{
    width: 100px;
    font-size: 16px;
    background-color: #10aeff;
}
.sy .weui-btn-area .link .register{
    float: right;
    margin: 0 20px;
}
.sy .weui-btn-area .link .login{
    float: left;
    margin: 0 20px;
}