.Login{
    width: 100%;
}

.Login h2{
    text-align: center;
    margin: 10px;
}
.Login .weui-cells_form{
    margin: 20px;
}
.Login .weui-cell{
    display: flex;
    line-height: 30px;
    padding: 10px;
}
.Login  .lines{
    border-bottom: 1px solid #f6f5f5;
}
.Login .js_input{
    padding: 10px;
}
.Login .button-sp-area{
    height: 50px;
    background: #0086b3;
    margin-top: 20px;
    line-height: 50px;
}
.Login .button-sp-area .weui-btn_cell{
    line-height: 17px;
    color: #fff;
    text-align: center;
    background-color: #1C89D3;
}

.Login .weui-flex{
    display: flex;
    padding: 9px;
    font-size: 14px;
}
.message{
    color: red;
}
.message_right{
    text-align: right;
}


