.Register .weui-btn_primary,
.Register .weui-btn_primary:hover{
    background-color: #1C89D3;
}
.Register .weui-form{
    position: relative;
    top: -18px;
}
.Register .weui-form__control-area {
    margin: 12px 0px;
}

.Register .weui-cells__group_form .weui-vcode-btn{
    color: #1C89D3;
}
.Register .isshow,.Register .ma{
    width: 100%;
    height: 350px;
    box-sizing: border-box;
    margin: 20px auto;
    position: absolute;
    top: 60px;
    background-color: white;
    z-index: 99;
    padding-top: 20px;
}

.Register .isshow .isshow-span1{
    display: inline-block;
    margin-left: 20px;
}

.Register .isshow .weui-cell_active1::before{
    display: none;
}

.Register .isshow .weui-cell_active1{
    margin-top: 30px;
}

.Register .isshow .isshow-img{
    display: inline-block;
    width: 150px;
    position: relative;
    top: 20px;
}

.Register .gologin{
    display: inline-block;
    width: 100%;
    text-align: right;
    font-size: 14px;
    position: relative;
    top: -14px;
    right: 18px;
}

.Register .isshow .isshow-false{
    color: red;
    position: relative;
    top:-20px;
    left: 30%;
}

.Register .gologin .gologintext{
    cursor: pointer;
    margin-right: 10px;
}

.Register .ma .ma2::before{
    display: none;
}

.Register .ma .ma1{
    margin-top: 10px;
}